.form{
  height: 800px;
}
.formo{
  width: 350px;
  height: 285px;
  background-color: #fff;
  margin: 0 auto;
  margin-top: -20px;
  overflow: hidden;

}
.form  .one{
  overflow: hidden;
  border-bottom: 1px solid #8b8b8b
}
.form img{
  width: 50px;
  height: 50px;
  display: block;
  float:left;
  margin:10px
}
.form .two span:nth-child(2)
{
  color:pink;
  display: block;
  line-height: 40px
}
.form .two span:nth-child(3){
  color: #8b8b8b;
  font-size: 12px
}
.form .two{
  width: 200px;
  float:left
}
.form .three{
  width: 150px;
  float:left;

}
.form .three span{
  width: 30px;
  line-height: 30px;
  display: block;
  float:left;
  text-align: center;
  margin-right: 10px;
  margin-top: 20px
}
.form .three span:nth-child(2){
  background-color: pink;
  border-radius: 50%
}
.main{
  width: 400px;
  line-height: 300px;
  background-color: hotpink;
  margin:0 auto;
  text-align: center;
  color:#fff;
  font-size: 30px;
  margin-top: 50px
}



